import React from 'react'
// import GatsbyLink from 'gatsby-link'
import Headroom from 'react-headroom'
import GatsbyLink from 'gatsby-link';

const Header = ({ siteTitle }) => (
  <Headroom
    style={{
      background: 'rebeccapurple',
      marginBottom: '1.45rem',
    }}
  >
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        padding: '1.45rem 1.0875rem',
      }}
    >
      <h1 style={{ margin: 0 }}>
        <GatsbyLink
          to="/"
          style={{
            color: 'white',
            textDecoration: 'none',
            marginRight: '20px'
          }}
        >
          Home
        </GatsbyLink>
        <GatsbyLink
          to="/events"
          style={{
            color: 'white',
            textDecoration: 'none',
          }}
        >
          Events
        </GatsbyLink>
      </h1>
    </div>
  </Headroom>
)

export default Header



